





























import Vue, { PropType } from "vue";
import { drinkData } from "../db/drink";

interface Menus {
  tab: string;
  content: string;
}

export default Vue.extend({
  name: "Drink",
  data() {
    return {
      drinks: drinkData,
      selected: ""
    };
  },
  methods: {
    addItem(name: string, price: number): void {
      this.$emit("addDrink", { name, price });
    }
  }
});
